/* /router.js 
 */

App.Router.map(function(){
   /**
    * @type link to index [root]
    */
   this.route("index", { path: "/" });

   this.route("job", { path: "/jobs" });
   
   /**
    * @type link to analyze
    */
   this.route("features", { path: "/features" });
   
   /**
    * @type link to form
    */
   this.route("form", { path: "/form" });
   
});
